<script>
import { mapActions, mapGetters } from "vuex";
import ThePagination from "../layout/ThePagination.vue";

export default {
  components: {
    ThePagination,
  },
  data() {
    return {
      isLoading: true,
      page: 1,
      limit: 10,
      query: null,
      item_counts: [10, 20, 30, 40, 50],
    };
  },
  watch: {
    limit() {
      this.resetList();
    },
    query() {
      this.resetList();
    },
    page() {
      this.loadHistoryList();
    },
  },
  methods: {
    ...mapActions({
      loadHistory: "balance/loadBalanceHistory",
    }),
    pageChanged(page) {
      this.page = page;
      this.loadHistoryList();
    },
    limitChanged(event) {
      this.limit = event.target.value;
    },
    resetList() {
      this.page = 1;
      this.loadHistoryList();
    },
    async loadHistoryList() {
      this.isLoading = true;
      try {
        await this.loadHistory({
          page: this.page,
          limit: this.limit,
          query: this.query,
        });
      } catch (err) {
        console.log(err);
        this.$store.commit("set_error", "Cannot load data please try again");
        this.$router.push({path:'/'})
      }
    },
  },
  computed: {
    ...mapGetters({
      history_list: "balance/get_history_list",
      total: "balance/get_total_count",
    }),
    page_count() {
      return Math.ceil((this.total || []) / this.limit);
    },
  },
  mounted() {
    this.loadHistoryList();
  },
};
</script>

